<template>
	<li  :class="['tab-item', {'tab-active': isActive}]" @click="selectTab">
		<span class="tab-text" ref="tab">
			<slot></slot>
			<div :class="['border-line', {'right': isRight}]"></div>
			<div v-if="count !== 0" class="count">{{count > 99 ? '99+' : count}}</div>
		</span></li>
</template>
<script>
export default {
	data() {
		return  {
			isRight: false
		}
	},
	props: {
		value: {
			type: [String, Number],
			default: 0
		},
		name: {
			default: '',
			type: String
		},
		count: {
			default: 0,
			type: Number
		},
		label: {
			default: '',
			type: String
		}
	},
	computed:{
		isActive() {
			return this.label == this.value 
		}
	},
	watch: {
		value(newValue, oldValue) {
			if(newValue === this.label) {
				if(this.label < oldValue) {
					this.isRight = true
				} else {
					this.isRight = false
				}
			} else {
				if(oldValue === this.label) {
					if(this.label > newValue) {
						this.isRight = false
					} else {
						this.isRight = true
					}
				}
			}
		}
	},
	methods: {
		selectTab(){
			this.$emit("input", this.label)
		}
	}
}
</script>
<style lang="scss">
.tab-item{
    flex: 1;
    vertical-align: top;
    text-align: center;
}
.tab-item.tab-active{
	color: #404040;
    font-weight: 600;

    .tab-text{
    	.border-line {
    		width: 100%;
    	}
    }
}

.tab-text{
		display: inline-block;
	    position: relative;

    .border-line {
		background: #4cc3ff;
	    border-radius: 34px;
	    height: 2px;
	    position: absolute;
	    bottom: 3px;

	    
	    margin: auto;
	    background-color: #ffe14c;
    }
    .count {
    	background: #4cc3ff;
	    border: 2px solid #4cc3ff;
	    color: #fff;
	    border-radius: 13px;
	    font-size: 20px;
	    height: 24px;
	    line-height: 20px;
	    letter-spacing: 0;
	    padding: 0 4px;
	    position: absolute;
	    top: 3px;
	    left: 85%;
	    box-sizing: border-box;
	    -webkit-transform-origin: 0 0;
	    transform-origin: 0 0;
	    transform: scale(.5);
	    min-width: 28px;
    }
}
</style>


