<template>
	<img class="item-true-img" :src="item.logo" alt="" @load="imgShow" @error="imgError">
</template>
<script>
export default {
	
	data() {
		return {
			isSuccess: false,
			isError: false
		}
	},
	props: ['item'],
	methods: {
		imgShow() {
			this.isSuccess = true;
			this.$set(this.item, 'isSuccess',true)
		},
		imgError() {
			this.isError = true;
			this.$set(this.item, 'isError',true)
		},
	},
	watch: {
		'item.isSuccess': function(val) {
			val || this.$set(this.item, 'isSuccess',this.isSuccess)
		},
		'item.isError': function(val) {
			val || this.$set(this.item, 'isError',this.isError)
		}
	}
}
</script>
<style lang="scss" scoped>
	.item-true-img{
		background: none;
    vertical-align: bottom;
	}
</style>


