<template>
	
	<div class="scroll-wrapper">
		<div v-show="showToTop" class="to-top" @click="goToTop"></div>
		<div class="scroll-container" ref="scrollparent" @scroll="onScroll" @touchstart="touchStart" @touchmove="touchMove" @touchend="touchEnd" :class="{'scroll-hardware': basicData.tracking}">
			<div class="scroll-content" ref="scroll" >
				<div class="pull-to-refresh-layer" ref="refresh">
					<div :class="{'before-pull-down-img': showImg, 'before-pull-down-gif': !showImg}">
                    </div>
                    <div class="before-pull-down-info">
                        <i class="anticon" :class="{'icon-down': showImg,'i-up': !refreshDown, 'icon-loading': !showImg}"></i>{{refreshInfo}}
                    </div>
				</div>
				<slot></slot>
			</div>
		</div>
	</div>
</template>
<script>
const getY = (node) => {
	let val = node.style.transform;
	let res;
	if(val) {
		// transform: translate3d(0px, 0px, 0px);
		// [
		// 	"translate3d(0px, 0px, 0px)", 
		// 	"3d", 
		// 	"0px,", 
		// 	"0px", 
		// 	", 0px", 
		// 	index: 11, input: "transform: translate3d(0px, 0px, 0px)", 
		// 	groups: undefined
		// ]
		res = /translate(Y|\dd)?\(\s*(\w+\s*,)?\s*([^,]+)(\s*,[^)]+)?\s*\)/.exec(val)

		if(res  === null){
			res = /matrix\(.*,\s*(\w+)\s*\)/.exec(val)
			if(res) {
				return res[1]
			}else {
				return null
			}
		} else {
			return res[3]
		}
	} else {
		return null
	}
} 
export default {

	data() {
		return {
			showToTop: false,
			isBottom: false,
			showImg: true,
			prevEnd: true,
			offsetHeight: 0,
			refreshInfo: "再拉，再拉就刷新给你看",
      		refreshInfoList: ["再拉，再拉就刷新给你看", "够了啦，松开人家嘛", "刷啊刷啊，好累啊，喵"],
			startTranslateY: 0,
			refreshDown: true, 
			isTouchDown: false,
			basicData: {
				isFirst: true,
				start: {},
				end: {},
				begin: true,
				tracking: false,
				prev: 0
			},
			// 返回顶部
			isTop: false,
			timer: null,

		}
	},
	props: {
		canScroll: {
			type: Boolean,
			default: false
		}
	},
	mounted() {
		 this.$refs.scroll.style.transform = `translateY(${this.startTranslateY}px)`;
		 this.offsetHeight = this.$refs.scrollparent.offsetHeight;
		 //window.addEventListener("scroll", this.onScroll)
	},
	methods: {
		refreshButtonEnd(){
			this.prevEnd = true
		},
		onScroll() {

			this.offsetHeight = this.$refs.scrollparent.offsetHeight;
			var scrollTop = this.$refs.scrollparent.scrollTop

			// 返回顶部
			this.isTop || clearInterval(this.timer)
			this.isTop = false;
			if(scrollTop > this.offsetHeight - 84){
				this.showToTop = true;
			} else {
				this.showToTop = false;
			}
			if(scrollTop +this.offsetHeight + 65 >=this.$refs.scroll.offsetHeight - 84 + 9 +1) {
				this.isBottom = true;
				this.prevEnd && (this.prevEnd = false, this.$emit("scroll-bottom", this.refreshButtonEnd, this))
				
			} else {
				this.isBottom = false;
			}


			/*if(scrollTop > 4 * window.innerHeight) {
				this.showToTop = true;
			} else {
				this.showToTop = false
			}
			// 手机上scrollTop数值可能使两个不相等 
			if(Math.ceil(scrollTop) + window.innerHeight >= document.body.clientHeight ) {
				this.isBottom = true;
				this.prevEnd && (this.prevEnd = false, this.$emit("scroll-bottom", refreshButtonEnd, this))
				
			} else {
				this.isBottom = false;
			}*/
		},
		goToTop() {
			clearInterval(this.timer);
			this.timer = setInterval(()=> {
				let scrollTop = this.$refs.scrollparent.scrollTop;
				let rate = Math.floor(-scrollTop / 7);
				this.$refs.scrollparent.scrollTop = scrollTop + rate;
				scrollTop === 0 && clearInterval(this.timer);
				this.isTop = true
			}, 30)
		},
		touchMove(e) {
			if(!this.canScroll) {
				// 当前元素剩下的监听函数将不会被执行
				e.stopImmediatePropagation();
				return false
			}
			this.type = e.type
			const translateY = parseFloat(getY(this.$refs.scroll))
			if("touchmove" === e.type){
				this.basicData.end.x = e.targetTouches[0].clientX;
				this.basicData.end.y = e.targetTouches[0].clientY;
			} else {
				this.basicData.end.x = e.clientX;
				this.basicData.end.y = e.clientY;
			}
			if(!this.basicData.prev) this.basicData.prev = this.basicData.start.y;
			const touchDistance = this.basicData.end.y - this.basicData.prev;
			this.basicData.prev = this.basicData.end.y;
			//必须位于顶部才向下执行 
			if(this.basicData.tracking && this.$refs.scrollparent.scrollTop <= 0) {
				// 向下拉
				if(this.basicData.end.y - this.basicData.start.y - this.basicData.start.scrollTop > 0) {
					const x = this.basicData.end.x - this.basicData.start.x
					const y = this.basicData.end.y - this.basicData.start.y -this.basicData.start.scrollTop
					if(!this.basicData.isFirst && this.isTouchDown){
						e.preventDefault();
						// 当前元素剩下的监听函数将不会被执行
						e.stopImmediatePropagation()
					} else if(!this.basicData.isFirst) {
						return false
					}

					if(Math.abs(x) > y)  {
						if(this.basicData.isFirst){
							this.basicData.isFirst = false;
							this.isTouchDown = false;
						}
						return false
					}

					if(this.basicData.isFirst) {
						this.isTouchDown = true;
						this.basicData.isFirst = false
					}

					if(y > 0) {
						event.preventDefault();
						event.stopPropagation();
						
						// 已经满足刷新条件后不往下执行
						//if(this.refreshInfo === this.refreshInfoList[1]) return
						// -webkit-overflow-scrolling 属性控制元素在移动设备上是否使用滚动回弹效果.
						// auto
						// 使用普通滚动, 当手指从触摸屏上移开，滚动会立即停止。
						// touch
						// 使用具有回弹效果的滚动, 当手指从触摸屏上移开，内容会继续保持一段时间的滚动效果。继续滚动的速度和持续的时间和滚动手势的强烈程度成正比。
						this.$refs.scrollparent.style.webkitOverflowScrolling = "auto";
						const moveDistance = touchDistance * 0.4 + translateY;
						this.$refs.scroll.style.transition = "";
						this.$refs.scroll.style.transform = `translateY(${moveDistance}px`;
						if(moveDistance > 106) {
							this.refreshInfo = this.refreshInfoList[1];
							this.refreshDown = false;
						} else{
							this.refreshInfo !== this.refreshInfoList[0] && (this.refreshInfo = this.refreshInfoList[0], this.refreshDown = true)
						}
					}


				}
			}
		},
		touchStart(e) {

			this.type = e.type
			//var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
			const translateY = parseFloat(getY(this.$refs.scroll))
			// 区别于addEventListener @touchstart
			if("touchstart" === e.type){
				// 多指触控时
				if(e.touches.length > 1) return void(this.basicData.tracking = false )
				
				if(0 === translateY){
					this.basicData.isFirst = true;
					this.basicData.tracking = true;
					this.basicData.start.x = e.targetTouches[0].clientX;
					this.basicData.start.y = e.targetTouches[0].clientY;
					this.basicData.start.scrollTop = this.$refs.scrollparent.scrollTop;
					this.basicData.prev = e.targetTouches[0].clientY;
					this.basicData.end.x = e.targetTouches[0].clientX;
					this.basicData.end.y = e.targetTouches[0].clientY;
				} else {
					this.basicData.tracking = false
				}

			} else {
				if(0 === translateY) {
					this.basicData.isFirst = true;
					this.basicData.tracking = true;
					this.basicData.start.x = e.clientX;
					this.basicData.start.y = e.clientY;
					this.basicData.start.scrollTop = this.$refs.scrollparent.scrollTop;
					this.basicData.prev = e.clientY;
					this.basicData.end.x = e.clientX;
					this.basicData.end.y = e.clientY;
				} else {
					this.basicData.tracking = false
				}
			}
		},
		touchEnd() {
			this.basicData.isFirst = true;
			this.isTouchDown = true;
			const translateY = parseFloat(getY(this.$refs.scroll))
			// touch
			// 使用具有回弹效果的滚动, 当手指从触摸屏上移开，内容会继续保持一段时间的滚动效果。继续滚动的速度和持续的时间和滚动手势的强烈程度成正比。
			this.$refs.scrollparent.style.webkitOverflowScrolling = "touch";

			if(this.basicData.tracking && parseFloat(this.startTranslateY) <= translateY && 0 == this.$el.scrollTop){
				
				this.refreshInfo = this.refreshInfoList[2];
				if(!this.refreshDown && this.showImg ) {

					this.showImg = false;
					this.$emit("refresh",this.refreshEnd, this)
				} else {
					this.refreshDown && this.showImg && this.refreshEnd();
					this.basicData.tracking = false;
				} 
			} else {
				this.refreshEnd()
			}
		}, 
		refreshEnd() {
			//重置状态
			this.refreshInfo = this.refreshInfoList[0];
			this.showImg = !0; 
			this.$refs.scroll.style.transform = `translateY(${this.startTranslateY}px)`;
			this.$refs.scroll.style.transition = "transform 0.5s";
			this.refreshDown = !0
		}
	}

}
</script>
<style lang="scss" scoped>
.scroll-wrapper{
	position: relative;
	height: 100%;
	width: 100%;
	overflow: hidden;
	.scroll-container{
		height: 100%;
	    width: 100%;
	    -webkit-overflow-scrolling: touch;
	        overflow: auto;
	}

}
.to-top {
	position: absolute;
    bottom: 94px;
    right: 12px;
    width: 45px;
    height: 45px;
    background: url(../../resoures/images/to-top.png) no-repeat;
    background-size: 100%;
    z-index: 2;
}
.pull-to-refresh-layer{
    height: 88px;
    width: 280px;
    margin: -97px auto 16px;
    .before-pull-down-img {
        width: 172px;
        height: 60px;
        background: url(../../resoures/images/refresh.png) no-repeat;
        background-size: 100%;
        margin: 0 auto 14px;
    }
    .before-pull-down-gif {
        width: 172px;
        height: 60px;
        background: url(../../resoures/images/refresh.gif) no-repeat;
        background-size: 100%;
        margin: 0 auto 14px;
    }
    .before-pull-down-info {
        text-align: center;
        line-height: 16px;
        color: #999;
        font-size: 14px;
        .icon-down {
            font-size: 19px;
            display: inline-block;
            transition: transform .5s;
            transform: rotate(0deg);
            &.i-up{
                transform: rotate(180deg);
            }
        }
        .icon-loading {
            display: inline-block;
            animation: waiting .5s linear infinite;
        }
    }
}
@keyframes waiting{
    0% {
        transform: rotate(0deg)
    }
    100% {
        transform: rotate(360deg)
    }
}

</style>


