<template>
	<div class="order-item" @click="showDetail">
		<div class="order-header">
			<div class="shop-name" ><div>{{shop.shop_name}}</div></div>
			
			<div class="order-text">{{shop.status_name}}</div>
		</div>

		<div class="order-content">
			<div class="content-wrapper"  v-if="shop && shop.rows && shop.rows.length > 1">
				<div class="img-wrapper"
				@touchstart="imgTouchStart"
				@touchmove="imgTouchMove"
				@touchend="imgTouchEnd">
					<div v-for="row in shop.rows">123456</div>
				</div>

			</div>
			<div class="content-wrapper single-item" v-else>
					<div class="item-img single-item-img" :class="{'item-img-error': shop && shop.rows && shop.rows[0].isError}" v-if="!(shop && shop.rows && shop.rows[0].isSuccess)"></div>
					<div class="item-img single-item-img " :class="{'item-img-success':shop && shop.rows && shop.rows[0].isSuccess}"  v-show="shop && shop.rows && shop.rows[0].isSuccess">
						<item-img :item="shop && shop.rows && shop.rows[0] || {}"></item-img>
					</div>
					<div class="item-content-right">
						<div class="item-name word-ellipsis-2">{{shop.rows[0].name}}</div>
		
						<div class="item-sku word-ellipsis-2">
							{{shop && shop.rows && shop.rows[0].extra_data | formatSku}}
						</div>
					</div>

			</div>
			<div class="total-wrapper">
				<div class="total">
					<span>{{shop.total_desc}}</span><span>¥</span><span class="price">{{Math.floor(shop.show_money / 100)}}.</span> <span class="price-decimal">{{Math.floor(shop.show_money / 100).toFixed(2) | getDecimal}}</span>
				</div>
			</div>
		</div>
		<div class="order-footer" v-if="op_json && op_json.length > 0">
			<div class="buttons" >
				<template v-for="(button,index) in op_json">
					<div :key="index" class="order-button" @click="(e)=>{e.stopPropagation(), handleOp(shop.rows)}">
						{{button.name}}
					</div>
				</template>
				
			</div>
		</div>
		

	</div>
</template>
<script>
import itemImg from './itemImg'
export default {
	props: {
		shop: {
			type: Object,
			default: null
		}
	},
	components: {
		itemImg
	},
	data() {
		return {
		}
	},
	methods: {
		showDetail(){},
		imgTouchStart(){},
		imgTouchMove(){},
		imgTouchEnd(){}
	},
	filters: {
		getDecimal(t) {
			return 12
		},
		formatSku(t) {
			return t ? t.skuSpec : ""
		}
	},
	computed: {
		op_json() {
			return this.shop.op_json ? this.shop.op_json.reverse() : this.shop.op_json
		}
	},
}
</script>
<style lang="scss" scoped>
	.order-item{
		margin-top: 10px;
	    padding: 0 0 0 12px;
	    box-sizing: border-box;
	    width: 100%;
	    background: #fff;
	    box-shadow: inset 0 0 0 0 #e7e7e7;
	}
	.order-header{
		display: flex;
	    position: relative;
	    margin-right: 12px;
	    height: 36px;
	    line-height: 36px;
	    background: #fff;
	    overflow: hidden;

	    &:after {
	    	content: "";
		    width: 100%;
		    position: absolute;
		    border-bottom: 1px solid #e7e7e7;
		    transform: scaleY(.5);
		    transform-origin: 0 0;
		    bottom: 0;
		    left: 0;
	    };
	    .shop-name {
		    position: relative;
		    max-width: 200%;
		    padding-right: 32px;
		    line-height: 36px;
		    font-size: 12px;
		    flex: 1;
		    transform-origin: left top;
	    }
	    .order-text{
	    	position: absolute;
		    top: 0;
		    right: 0;
		    font-size: 12px;
		    color: #212121;
		    letter-spacing: 0;
	    }
	}
	.order-content{
		background: #FFF;

		.content-wrapper{
			height: 100px;
			position: relative;
		    padding: 10px 12px 10px 0;
		    height: 100px;
		    overflow: hidden;

		    .item-img{
		    	 width: 80px;
			    height: 80px;
			    margin-left: 6px;
			    background: #eee url(../../resoures/images/picture-loading.svg) no-repeat 50%/40px 40px;

			    img{
			    	position: absolute;
				    border-radius: 4px;
				    top: 50%;
				    left: 50%;
				    width: 100%;
				    min-height: 100%;
				    object-fit: cover;
				    transform: perspective(1px) translate(-50%,-50%);
			    }
		    }

		    .item-img-success{
		    	 position: relative;
			    display: flex;
			    align-items: center;
			    background: #eee;
			    overflow: hidden;
			    border-radius: 4px;
		    }
		    .item-img-error{
				background: #eee url(../../resoures/images/failed-img.svg) no-repeat 50%/40px 40px;
		    }
		}
		.single-item{
			display: flex;
    		height: 100px;

    		.item-img{
				margin-left: 0px;
    		}
    		.item-content-right{
    			 position: relative;
			    flex: 1 1;
			    height: 100%;
			    margin-left: 10px;
    		}
    		.item-name {
    			line-height: 20px;
			    font-size: 14px;
			    color: #212121;
			    letter-spacing: 0;
    		}
    		.item-sku{
    			    position: absolute;
			    bottom: 0;
			    width: 100%;
			    font-size: 12px;
			    letter-spacing: 0;
			    color: #999;
    		}
		}

		.total-wrapper {
			height: 40px;
		    padding-top: 10px;
		    overflow: hidden;

		    .total{
		    	text-align: right;
		    	padding-right: 12px;

		    	span {
		    		    display: inline-block;
		    		font-size: 12px;
				    line-height: 12px;
				    color: #212121;
		    	}
		    	.price-decimal, .price{
					font-size: 16px;
					line-height: 16px;
					color: #000;
					font-weight: 700;
		    	}
		    }
		}
	}
	.order-footer{
		padding-right: 12px;
	    height: 42px;
	    overflow: hidden;
		.buttons {
			 float: right;
		    padding-bottom: 12px;
		    overflow: hidden;
		}
	    .order-button{
	    	    height: 30px;
			    min-width: 80px;
			    padding: 0 12px;
			    text-align: center;
			    line-height: 30px;
			    color: #fff;
			    font-size: 14px;
			    letter-spacing: 0;
			    background-image: linear-gradient(-236deg, #4cc3ff, #4cc3ff);
			    box-shadow: 0 2px 4px 0 rgba(28,140,193,0.7);
			    border-radius: 100px;
	    }
	}


</style>


