import Vue from 'vue'
import error from '../../components/Error/Error'
import loading from '../../components/Loading/Loading'
import navBar from '../../components/NavBar/NavBar'
import tabItem from '../../components/TabItem/TabItem'
import slider from '../../components/Slider/SliderContainer'
import orderItem from '../../components/orderItem/orderItem'
import scroller from '../../components/Scroller/order-list-scroller'
import flexible from '../../utils/simple-flexible'
import  '../../resoures/css/animate.css'



import  '../../resoures/css/main.scss'
import  '../../resoures/css/common.scss'
import  '../../resoures/font/iconfont.css'

import './orderlist.scss'
import api from '../../models/api'
import * as utils from '../../utils/utils'
import  uInterface from '../../utils/user-interface'
//import fastclick from 'fastclick';
import 'babel-polyfill';
//import 'lodash'
flexible()


// FOR H5 处理移动端click 延时 300ms的问题
// fastclick.attach(document.body);

// 根据当前环境选择性加载 mock 包，发布的时候也不用注释和删除
if(process.env.NODE_ENV === 'development') {
  require('./mock');
}



new Vue({
  el: '#app',
  data() {
    return {
      linkIcons: [],
      canTouch: true,
      pageSize: 10,
      isLogined: false,
      selectedTab: 0,
      tabEmptyLists: [false,false,false,false,false],
      tabErrorLists: [false,false,false,false,false],
      tabLoadingLists: [true,true,true,true,true],
      tabHasLoading: [false,false,false,false,false],
      menus: [],
      tabPages: [{
        current: 0,
        isSubLoading: true,
        isEnd: true
      },{
        current: 0,
        isSubLoading: true,
        isEnd: true
      },{
        current: 0,
        isSubLoading: true,
        isEnd: true
      },{
        current: 0,
        isSubLoading: true,
        isEnd: true
      },{
        current: 0,
        isSubLoading: true,
        isEnd: true
      }],
      tabLists: {
        1: [],
        2: [],
        3: [],
        4: [],
        0: [],
      },
      tabNoticeLists: [null,null,null,null,null],
      tabsCount: [0,0,0,0,0],
      emptyTexts: ["暂无订单", "暂无待开补款订单", "暂无待开付款订单", "暂无待收货订单", "暂无已完成订单"],
    }

  },
  created() {
    utils.handleStorage(() => {
      this.actionRefresh();
    });
    try {
      this.selectedTab = utils.getQueryString("hashstatus") || utils.getQueryString("status") || 0;
    } 
    catch (e){
      this.selectedTab = 0;
    }
    this.getCount();

    this.tabHasLoading[this.selectedTab] || this.getList(this.selectedTab);

    //xw.config
    
  },
  mounted() {
      window.onpageshow = function(t) {
        t.persisted && window.location.reload()
      }
  },
  components: {
    slider,
    scroller,
  	error,
  	loading,
    navBar,
    tabItem,
    orderItem
  },

  methods: {
    go(url){
      location.href = url
    },
    back() {
      utils.back();
    },
    refresh() {
      this.getList();
    },
    refreshBottom(refreshButtonEndFunc, _this){
      console.log("onbottom")
     if(this.tabPages[this.selectedTab].isEnd) return false;
     this.tabPages[this.selectedTab].current+=1;
     this.getList(this.selectedTab, true, false, ()=>{
      _this.refreshButtonEnd.call(_this)
     })
      
    },
    refreshDown(refreshEndFunc, _this){
      this.tabPages.forEach(page => {
        page.current = 0;
        page.isSubLoading = true;
        page.isEnd = false;
      })

      

      this.getList(this.selectedTab, false, false, ()=> {
        _this.refreshEnd.call(_this);
        //_this.refreshButtonEnd.call(_this);
        this.$refs.scroller[this.selectedTab].$refs.scrollparent.scrollTop = 0
      })
    },
    goToLogin(){
      uInterface.goToLogin(location.href)
    },
    actionRefresh() {

    },
    pageChange(currentTab) {
      this.selectedTab = currentTab;
      utils.replaceQueryParam('hashstatus',currentTab);
      this.tabHasLoading[currentTab] || this.getList();
      //this.getList();
      console.log("change")
    },

    getCount(){
      api.getCount().then(({data})=> {
        data.list = data.list || [];
        data.list.forEach((item,index)=> {
          item.index = index;
        })
        this.menus = data.list ;
        this.linkIcons = data.right_top_icons
      })
    },

    getList(tabIndex = this.selectedTab, isBottom,isLoading = true,callback = function() {}) {
      this.$set(this.tabErrorLists, tabIndex, false)
      this.$set(this.tabEmptyLists, tabIndex, false)
      isLoading && this.$set(this.tabLoadingLists, tabIndex, true);

      const pageNum = this.tabPages[tabIndex].current;
      this.tabHasLoading[this.selectedTab] = true

      api.getOrderList({
        pageNum,
        pageSize:this.pageSize,
        status: tabIndex, 
        customer: 0})
        .then(({data,status,msg}) => {
          this.isLogined = true;
          this.tabPages[tabIndex].isSubLoading = false;

          if(isBottom){
            this.$set(this.tabLists, tabIndex, [...this.tabLists[tabIndex], ...data.list])
          } else {
            console.log("refresh")
            this.$set(this.tabLists, tabIndex, data.list)
          }

          if(this.tabNoticeLists[this.selectedTab]) {
            this.tabNoticeLists[this.selectedTab] = Object.assign({}, this.tabNoticeLists[this.selectedTab], data.notice)
          } else {
            this.tabNoticeLists[this.selectedTab] = data.notice
          }
          for (let i in this.tabNoticeLists) {
            if(this.tabNoticeLists[i] && !this.tabNoticeLists[i].show){
              this.tabNoticeLists[i].show = "show"
            }
          }
          if(0 === data.total) {
            this.$set(this.tabEmptyLists, true)
          }

          const total = pageNum * this.pageSize + data.list.length;
          console.log(total)

          this.tabPages[tabIndex].isEnd = total >= data.total

          this.$set(this.tabLoadingLists, tabIndex, false)

          this.$set(this.tabsCount,tabIndex,data.total)

          callback()
        }).catch((i)=>{

          //n.isLogined = !(i && i.body && 2 === i.body.errno), n.tabPages[t].isSubLoading = !1, o(), e || n.$set(n.tabLists, t, []), n.$set(n.tabErrorLists, t, !0), n.$set(n.tabLoadingLists, t, !1)
          this.isLogined = false;
          this.tabPages[tabIndex].isSubLoading = false
          this.$set(this.tabLoadingLists, tabIndex, false)
          this.$set(this.tabErrorLists,tabIndex, true)
          callback();
          if(!isBottom) {
            this.$set(this.tabLists,tabIndex,[])
          }
        })
    },

  },
    watch: {
    isLogined(t) {
      if(t) {
        this.tabHasLoading[this.selectedTab] || this.getList()
      }
    }
  }

  
})


